<template>
	<u-index-list v-if="!loding" :scrollTop="scrollTop" :index-list="indexList">
		<template v-for="(item, index) in list">
			<u-index-item>
				<!-- 	<u-index-anchor :text="indexList[index]"></u-index-anchor> -->
